import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { SettingService } from 'src/app/services/setting.service';
import { ToastrService } from 'ngx-toastr';
import { AuthLayoutComponent } from '../../auth-layout.component';
import { Constants } from 'src/app/constants';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {

  category: any;
  posts: any;

  p = 1;
  pageSize = Constants.PostPerPage;

  constructor(
    private route: ActivatedRoute,
    private userService: UserService,
    private settingService: SettingService,
    private toastrService: ToastrService,
    private authLayoutComponent: AuthLayoutComponent,
  ) {
    this.route.params.subscribe(
      params => {
        this.settingService.getCategery(params.id).subscribe(
          data => {
            this.category = data.data;
            setTimeout(() =>
              this.authLayoutComponent.setPageTitle(
                this.category.title, this.category.description
              )
              , 100);
          }
        );

        this.settingService.getCategeryPosts(params.id).subscribe(
          data => {
            this.posts = data.data;
          }
        );
      }
    );
  }

  ngOnInit(): void {
  }

}
