import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/user.service';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthLayoutComponent } from '../../auth-layout.component';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  client = {
    email: '',
    type: 'client',
    first_name: '',
    last_name: '',
    password: '',
    agreed: '',
    referral_id: ''
  };

  referral: any;

  constructor(
    private userService: UserService,
    private toastrService: ToastrService,
    private router: Router,
    private route: ActivatedRoute,
    private authLayoutComponent: AuthLayoutComponent
  ) { }

  ngOnInit() {
    setTimeout(() =>
      this.authLayoutComponent.setPageTitle(
        'Register',
        'Kindly Register Your Details To Use The System'
      )
      , 100);
    this.route.queryParams.subscribe(
      data => {
        if ('id' in data) {
          console.log(data);
          const email = this.userService.decryptEmail(data.id);
          this.userService.getClient(email).subscribe(
            userData => {
              if (userData.status === 'success') {
                this.client.referral_id = userData.data.id;
                this.referral = userData.data;
              }
            }
          );
        }
      }
    );
  }

  register() {
    this.userService.createClient(this.client).subscribe(
      data => {
        if (data.status === 'success') {
          this.toastrService.success(data.message);
          this.userService.login(this.client).subscribe(
            loginData => {
              if (loginData.status === 'success') {
                this.toastrService.success(loginData.message);
                this.router.navigate([`/${this.client.type}`]);
              } else {
                this.toastrService.error(loginData.message);
              }
            }
          );
        } else {
          this.toastrService.error(data.message);
        }
      }
    );
  }
}
