import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { SettingService } from 'src/app/services/setting.service';
import { ToastrService } from 'ngx-toastr';
import { AuthLayoutComponent } from '../../auth-layout.component';
import { Constants } from 'src/app/constants';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css']
})
export class PostComponent implements OnInit {
  new_comment = {
    post_id: 0,
    comment: ''
  };

  p = 1;
  pageSize = Constants.CommentPerPage;

  post: any;

  comments = [];
  // posts: any;

  constructor(
    private route: ActivatedRoute,
    public userService: UserService,
    private settingService: SettingService,
    private toastrService: ToastrService,
    private authLayoutComponent: AuthLayoutComponent,
  ) {
    this.route.params.subscribe(
      params => {
        this.settingService.getPost(params.id).subscribe(
          data => {
            this.post = data.data;
            setTimeout(() =>
              this.authLayoutComponent.setPageTitle(
                this.post.title, this.post.abstract
              )
              , 100);
          }
        );
        this.settingService.getPostComments(params.id).subscribe(
          data => {
            this.comments = data.data;
          }
        );
      }
    );
  }


  ngOnInit(): void {
  }

  postComment() {
    this.new_comment.post_id = this.post.id;

    this.settingService.createComment(this.new_comment).subscribe(
      data => {
        if (data.status === 'success') {
          this.new_comment = {
            post_id: 0,
            comment: ''
          };
          this.comments.unshift(data.data);
          this.toastrService.success(data.message);
        } else {
          this.toastrService.error(data.message);
        }
      }
    );
  }

}
